import { TextStyle, Page, Layout, EmptyState, FooterHelp, Link, Card, Stack, Heading, Subheading, FormLayout, TextField } from '@shopify/polaris';
import { TitleBar, ResourcePicker } from '@shopify/app-bridge-react';
import { useState } from 'react';

const register = () => {

    const [storeName, setStoreName] = useState('Nombre de la tienda');
    const handleChangeStoreName = useCallback((newValue) => setStoreName(setStoreName), []);

    const [email, setEmail] = useState('E-Mail');
    const handleChangeEmail = useCallback((newValue) => setEmail(email), []);

    const [legalRep, setLegalRep] = useState('Nombre del Representante Legal');
    const handleChangeLegalRep = useCallback((newValue) => setLegalRep(legalRep), []);

    const [creditCard, setCreditCard] = useState('Opcional (Tarjeta De Crédito');
    const handleChangeCreditCard = useCallback((newValue) => setCreditCard(creditCard), []);

    const [phone, setPhone] = useState('Teléfono');
    const handleChangePhone = useCallback((newValue) => setPhone(phone), []);

    return(
            <Page fullWidth={true}>
                <Layout>
                    <Layout.Section>
                        <Card>
                                <Heading element="h1">Registro</Heading>
                        </Card>
                    </Layout.Section>
                </Layout>
                <FormLayout>
                    <FormLayout.Group>
                        <TextField type="text" label="Nombre de la tienda" onChange={handleChangeStoreName} />
                        <TextField type="text" label="E-Mail" onChange={handleChangeEmail} />
                    </FormLayout.Group>
                    <FormLayout.Group>
                        <TextField type="text" label="Nombre del Representante Legal" onChange={handleChangeLegalRep} />
                        <TextField type="text" label="Opcional (Tarjeta De Crédito" onChange={handleChangeCreditCard} />
                    </FormLayout.Group>
                    <FormLayout.Group>
                        <TextField type="text" label="Teléfono" onChange={handleChangePhone} /> 
                    </FormLayout.Group>
                </FormLayout>



            </Page>
    );
};
export default register;